#about-section{background-image:url("../assets/about_home.jpg");background-size:cover;background-position:center;background-attachment:fixed;color:#f4f3ee;padding:4rem 2rem;position:relative}#about-section::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.5);z-index:1}#about-section .about-content{position:relative;z-index:2;max-width:800px;margin:0 auto;text-align:center}#about-section .about-content h2{font-family:"Work Sans",serif;font-weight:bold;font-size:4rem;padding:1rem 4rem;margin-bottom:1.5rem;color:#b0c4de;width:fit-content;justify-self:center;border-bottom:3px solid #b0c4de;text-transform:uppercase}#about-section .about-content .description{font-family:"Work Sans",serif;font-size:1rem;line-height:1.8;margin-bottom:2rem;color:#f4f3ee}#about-section .about-content .skills-list{list-style:none;padding:0;margin:0;display:flex;flex-wrap:wrap;margin-bottom:2rem;gap:1rem;justify-content:center}#about-section .about-content .skills-list li{font-size:1rem;font-family:"Poppins",serif;line-height:1.6;position:relative;padding-left:2rem;color:#f4f3ee}#about-section .about-content .skills-list li:before{content:"•";margin-right:1rem;font-size:1.5rem;color:#b0c4de}#about-section .about-content .skills-list li .highlight{color:#d4af37}#about-section .about-content .cta-button{background-color:#d4af37;color:#1b263b;border:none;padding:.75rem 1.5rem;font-size:1.3rem;font-weight:bold;text-transform:uppercase;font-family:"Work Sans",serif;cursor:pointer;transition:background-color .3s}#about-section .about-content .cta-button:hover{background-color:#b0c4de}